:root {
  --color-text-main: #fff;
  --color-text-black: #000;
  --color-bg-black: #000;
  --color-bg-green: #d7fe39;
  --color-bg-blue: #3fa9f5;
  --font-family: "Inter", sans-serif;
  --second-family: "Craftwork Grotesk", sans-serif;
}

@font-face {
  font-display: swap;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/inter-v18-cyrillic_latin-regular.woff2") format("woff2"), url("../fonts/inter-v18-cyrillic_latin-regular.ttf") format("truetype");
}
@font-face {
  font-display: swap;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/inter-v18-cyrillic_latin-500.woff2") format("woff2"), url("../fonts/inter-v18-cyrillic_latin-500.ttf") format("truetype");
}
@font-face {
  font-family: "Craftwork Grotesk";
  src: url("../fonts/CraftworkGrotesk-Bold.woff2") format("woff2"), url("../fonts/CraftworkGrotesk-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
/*===== global styles =====*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  min-width: 320px;
  scrollbar-gutter: stable;
  scroll-behavior: smooth;
}

body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--color-text-main, #fff);
  background-color: var(--color-bg-black, #000);
  font-family: var(--font-family, "Inter", sans-serif);
  font-weight: 400;
}

body.lock {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1512px;
  margin: 0 auto;
  padding: 0 36px;
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

a {
  color: inherit;
  text-decoration: none;
  outline: none;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

ol,
ul {
  list-style: none;
}

button,
input {
  padding: 0;
  font-family: inherit;
  font-size: inherit;
  border: none;
  border-radius: 0;
  appearance: none;
  outline: none;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

.header {
  padding: 14px 0;
}
@media (max-width: 991px) {
  .header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    padding: 16px 0;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
    background: var(--color-bg-black, #000);
  }
  .header.active .header__navbar {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    transform: translate(0, 0);
  }
}

.header__container {
  position: relative;
  display: flex;
  gap: 0 30px;
  align-items: center;
  justify-content: space-between;
}

.header__home {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 50px;
  height: 50px;
}
.header__home img {
  display: inline-block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
@media (max-width: 991px) {
  .header__home {
    position: relative;
    z-index: 11;
    width: 42px;
    height: 42px;
  }
}

.header__navbar {
  display: flex;
  align-items: center;
  gap: 0 30px;
  transition: none;
}
@media (max-width: 991px) {
  .header__navbar {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    overflow: auto;
    display: block;
    width: 100%;
    height: 100dvh;
    padding: 80px 16px 20px;
    background-color: var(--color-bg-black, #000);
    transform: translate(0, 30px);
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: transform 0.3s ease-out, opacity 0.3s ease-out, visibility 0.3s ease-out;
  }
}

.header__menu {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4px;
  border: 1px solid rgba(255, 255, 255, 0.17);
  border-radius: 20px;
  backdrop-filter: blur(40px);
  box-shadow: 0 1px 2px 0 rgba(45, 48, 57, 0.03), 0 2px 4px 0 rgba(45, 48, 57, 0.04), 0 4px 8px 0 rgba(45, 48, 57, 0.05), 0 7px 14px 0 rgba(45, 48, 57, 0.05), 0 12px 26px 0 rgba(45, 48, 57, 0.06), 0 30px 63px 0 rgba(45, 48, 57, 0.09);
  background: rgba(255, 255, 255, 0.08);
}
@media (max-width: 1200px) {
  .header__menu {
    position: static;
    transform: translate(0, 0);
  }
}
@media (max-width: 991px) {
  .header__menu {
    display: block;
    margin-bottom: 20px;
  }
}

.header__link {
  position: relative;
  display: inline-block;
  padding: 12px 11px;
  font-size: 15px;
  font-weight: 500;
  white-space: nowrap;
}
.header__link::after {
  content: "";
  position: absolute;
  bottom: 5px;
  left: 11px;
  right: 11px;
  height: 1px;
  background: var(--color-text-main, #fff);
  opacity: 0;
  transform: scale(0.2, 1);
  transition: opacity 0.2s ease-out, transform 0.2s ease-out;
}
@media (hover: hover) {
  .header__link:hover::after {
    opacity: 1;
    transform: scale(1, 1);
  }
}

.header__link--amount span {
  display: inline-block;
  min-width: 18px;
  min-height: 18px;
  margin-left: 6px;
  padding: 1px 3px;
  font-size: 14px;
  letter-spacing: -0.01em;
  text-align: center;
  color: var(--color-text-black, #000);
  background-color: var(--color-bg-green, #d7fe39);
  border-radius: 6px;
}

.header__link--gradient {
  background: linear-gradient(90deg, #d7fe39 0%, #55fe39 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.header__link--gradient::after {
  background: linear-gradient(90deg, #d7fe39 0%, #55fe39 100%);
}

.header__user {
  position: relative;
}
.header__user.active .header__user-toggle:after {
  transform: scaleY(-1);
}
.header__user.active .header__user-dropdown {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  transform: translateY(0);
}

.header__user-toggle {
  display: flex;
  gap: 0 10px;
  align-items: center;
  min-height: 50px;
  padding: 5px 10px 5px 5px;
  border-radius: 16px;
}
.header__user-toggle picture {
  display: inline-block;
  width: 40px;
  height: 40px;
}
.header__user-toggle img {
  display: inline-block;
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 50%;
  overflow: hidden;
}
.header__user-toggle:after {
  content: "";
  display: inline;
  width: 18px;
  height: 18px;
  background-image: url("data:image/svg+xml,%3Csvg width='18' height='18' viewBox='0 0 18 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.5 6.75L9 11.25L13.5 6.75' stroke='white' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round' /%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transition: transform 0.2s ease-out;
}
@media (max-width: 991px) {
  .header__user-toggle {
    display: none;
  }
}

.header__user-name {
  font-size: 15px;
}

.header__user-dropdown {
  position: absolute;
  top: 110%;
  right: 0;
  z-index: 5;
  width: 100%;
  min-width: 200px;
  padding: 4px;
  border: 1px solid rgba(255, 255, 255, 0.17);
  border-radius: 20px;
  backdrop-filter: blur(40px);
  box-shadow: 0 1px 2px 0 rgba(45, 48, 57, 0.03), 0 2px 4px 0 rgba(45, 48, 57, 0.04), 0 4px 8px 0 rgba(45, 48, 57, 0.05), 0 7px 14px 0 rgba(45, 48, 57, 0.05), 0 12px 26px 0 rgba(45, 48, 57, 0.06), 0 30px 63px 0 rgba(45, 48, 57, 0.09);
  background: rgba(255, 255, 255, 0.08);
  background: var(--color-bg-black, #000);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(10px);
  transition: opacity 0.2s ease-out, visibility 0.2s ease-out, transform 0.2s ease-out;
}
@media (max-width: 991px) {
  .header__user-dropdown {
    position: static;
    transform: none;
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }
}

.header__user-link {
  position: relative;
  display: inline-block;
  padding: 12px 11px;
  font-size: 15px;
  font-weight: 500;
  white-space: nowrap;
  text-align: right;
}
.header__user-link::after {
  content: "";
  position: absolute;
  bottom: 5px;
  left: 11px;
  right: 11px;
  height: 1px;
  background: var(--color-text-main, #fff);
  opacity: 0;
  transform: scale(0.2, 1);
  transition: opacity 0.2s ease-out, transform 0.2s ease-out;
}
@media (hover: hover) {
  .header__user-link:hover::after {
    opacity: 1;
    transform: scale(1, 1);
  }
}

.header__burger {
  display: none;
}
@media (max-width: 991px) {
  .header__burger {
    position: relative;
    z-index: 11;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 42px;
    height: 42px;
  }
  .header__burger.active .header__burger-icon::before, .header__burger.active .header__burger-icon::after {
    top: 50%;
    bottom: unset;
  }
  .header__burger.active .header__burger-icon::after {
    transform: rotate(45deg) translate(0, 0);
  }
  .header__burger.active .header__burger-icon::before {
    transform: rotate(-45deg) translate(0, 0);
  }
  .header__burger.active .header__burger-icon span {
    opacity: 0;
  }
}

.header__burger-icon {
  position: relative;
  display: block;
  width: 27px;
  height: 18px;
}
.header__burger-icon span, .header__burger-icon::before, .header__burger-icon::after {
  position: absolute;
  top: 50%;
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  background-color: var(--color-text-main, #fff);
  border-radius: 2px;
  transform: translate(0, -50%);
  transition: opacity 0.3s ease-out, transform 0.3s ease-out;
}
.header__burger-icon::before, .header__burger-icon::after {
  content: "";
  transform: translate(0, 0);
}
.header__burger-icon::before {
  top: 0;
}
.header__burger-icon::after {
  top: unset;
  bottom: 0;
}

.main {
  flex-grow: 1;
}